import uCharts from '@/components/u-charts/u-charts.js';
import  { isJSON } from '@/common/checker.js';
var _self;
var canvaArea=null;
  
export default {
	data() {
		return {
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			textarea:''
		}
	},
	onLoad() {
		_self = this;











		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data:{
				},
				success: function(res) {
					console.log(res.data.data)
					let Area={categories:[],series:[]};
					//这里我后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					Area.categories=res.data.data.Area.categories;
					Area.series=res.data.data.Area.series;
					_self.textarea = JSON.stringify(res.data.data.Area);
					_self.showArea("canvasArea",Area);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		showArea(canvasId,chartData){
			canvaArea=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'area',
				fontSize:11,
				padding:[0,15,10,15],
				legend:{
					show:true,
					position:'top',
					float:'center',
					itemGap:30,
					padding:5,
					lineHeight:18,
					margin:0,
				},
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type:'grid',
					gridColor:'#CCCCCC',
					gridType:'dash',
					dashLength:8,
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#CCCCCC',
					dashLength:8,
					splitNumber:5,
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					area:{
						type: 'curve',
						opacity:0.2,
						addLine:true,
						width:2,
						gradient:false
					}
				}
			});
			
		},
		touchArea(e) {
			canvaArea.touchLegend(e);
			canvaArea.showToolTip(e, {
				format: function (item, category) {
					return category + ' ' + item.name + ':' + item.data 
				}
			});
		},
		changeData(){
			if(isJSON(_self.textarea)){
				let newdata=JSON.parse(_self.textarea);
				canvaArea.updateData({
					series: newdata.series,
					categories: newdata.categories
				});
			}else{
				uni.showToast({
					title:'数据格式错误',
					image:'../../../static/images/alert-warning.png'
				})
			}
		}
	}
}
